@import '../../foundations/var.scss';

.c-header__hamburger {
  width: 48px;
  height: 100%;
}

.c-hamburger {
  background-color: transparent;
  border-color: transparent;
  z-index: 9999;

  @include spm {
    display: none;
  }

  span {
    width: 100%;
    height: 1px;
    background-color: #000;
    position: relative;
    transition: ease .4s;
    display: block;

    &:nth-child(1) {
      top: 0;
    }

    &:nth-child(2) {
      margin: 8px 0;
    }

    &:nth-child(3) {
      top: 0;
    }
  }

  &.active span {
    &:nth-child(1) {
      top: 5px;
      transform: rotate(45deg);
    }

    &:nth-child(2) {
      opacity: 0;
    }

    &:nth-child(3) {
      top: -13px;
      transform: rotate(-45deg);
    }
  }
}